.product-card {
  background-color: #f7a440 !important;
  margin: 10px 5px;
  height: 30rem;
  max-width: 18rem;
  min-width: 220px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.card-img-top {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.btn-add-cart {
  position: absolute;
  bottom: 1rem;
  background-color: #e1701a;
  border-color: #e1701a;
}

.btn-div {
  height: 50px;
}
