.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.col-padding {
  padding: 0px;
}

.brand-name-extra {
  margin: 0px !important;
}

.navbar-rtl {
  align-items: start;
}

.btn-view-cart {
  background-color: #f6dcbf;
  width: 200px;
}

.navbar-bg {
  background-color: #e1701a;
}

#btn-search {
  height: 40px;
}

#search-field {
  width: 300px;
  height: 40px;
  margin-left: 10px;
}

.search-bar {
  width: 600px;
}

#fullMenu {
  width: 50%;
}
